.goodsItem{
    background: #FFFFFF;
    border-radius: 10px;
    margin: 10px 15px 5px 15px;
}

.goodsItem__title{
    display: flex;
    justify-content: space-between;
    padding: 10px 30px 0;
}

.goodsItem__name{
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    color: #000000;
}

.goodsItem__sum{
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    color: #000000;
}

.goodsItem__edit{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.goodsItem__countGoods{
    margin: 0 20px;
}

.catalogSectionItem__minus{
    background: url('/src/components/CatalogSectionItem/img/minus.png');
    background-repeat: no-repeat;
    background-size: 100%;
    border: none;
    width: 40px;
    height: 40px;
}

.catalogSectionItem__plus{
    background: url('/src/components/CatalogSectionItem/img/plus.png');
    background-repeat: no-repeat;
    background-size: 100%;
    border: none;
    width: 40px;
    height: 40px;
}

@media (max-width: 500px){
    .goodsItem{
        width: 180px;
        padding: 5px;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 5px auto;
    }

    .goodsItem__title{
        padding: 10px;
    }

    .goodsItem__name, .goodsItem__sum{
        font-size: 13px;
        line-height: 15px;
    }

    .goodsItem__edit {
        width: 130px;
        justify-content: center;
    }

    .basket.fix{
        position: relative;
    }
}